






























































































*{
  box-sizing: border-box;
}
.drop-wraper{
  margin: 0;
  width: 100%;
}
.drop-wraper textarea{
  margin: 0;
  width: 100%;
  min-height:200px;
  border: 1px solid #ddd;
  border-bottom: 1px dashed #ddd;
  font-size: 14px;
  background-color: #fafafa;
  display: block;
}
.is-drag-over {
  box-shadow: #c9ff00 0 0 3px;
}
.drag-info {
  padding: 7px 10px;
  color: #767676;
  line-height: 16px;
  margin: 0;
  font-size: 12px;
  border: 1px solid #ddd;
  border-top: 0;
  background-color: #fafafa;
}



